import React from "react";
import "./Square.css";

export default function Square(props) {
  // const [value, setValue] = useState(null);

  // function handleClick() {
  //   setValue("X");
  // }

  const color = props.color;

  return (
    <button
      onClick={props.onSquareClick}
      className="square glow-on-hover"
      style={{ color: color }}
    >
      {props.value}
    </button>
  );
}



